<template>
  <div class="frenteTrabalho">
      <v-container>
        <v-layout row wrap>
          <v-flex sm12 xs12 md12 lg12 class="mt-10">
              <v-card-title class="align-start">
                <v-sheet
                color="brown"
                width="100%"
                class="overflow-hidden mt-n9 transition-swing v-card--material__sheet"
                style="z-index: 2"
                elevation="6"
                max-width="100%"
                rounded
                >
                  <v-theme-provider dark>
                  <slot name="heading" />
                  <div class="py-3">
                    <v-row class="px-5 d-flex">
                      <v-col
                        cols="1"
                        sm="1"
                        md="1"
                        lg="1"
                        xs="1"
                        class="text-left"
                      >
                        <v-btn
                          depressed
                          color="transparent"
                          class="text-left"
                          @click="returnHome"
                        >
                          <v-icon dark small>
                            mdi-arrow-left-bold-circle mdi-36px
                          </v-icon>
                        </v-btn>
                      </v-col>
                      <v-col
                        cols="11"
                        sm="11"
                        md="11"
                        lg="11"
                        xs="11"
                        class="text-center"
                      >
                        <span class="text-h7 white--text  v-card--material__title pr-15">Frente De Trabalho</span>
                      </v-col>
                    </v-row>
                  </div>
                </v-theme-provider>
                </v-sheet>
              </v-card-title>
              <v-card class="card-funcao">
                <v-container class="py-0">
                  <v-row class="pt-9 px-5">
                    <template>
                      <v-col
                        cols="12"
                        sm="4"
                        md="4"
                        lg="5"
                        xs="5"
                      >
                        <v-text-field
                        v-model="textFilter"
                          clearable
                          label="Pesquisar"
                          append-icon="mdi-magnify"
                        ></v-text-field>
                      </v-col>
                      <v-col
                        cols="12"
                        sm="4"
                        md="4"
                        lg="2"
                        xs="2"
                        style="text-align:right"
                      >
                        <v-switch
                          v-model="switchFrenteTrabalho"
                          label="Mudar Status"
                        >Status</v-switch>
                      </v-col>
                      <v-col
                        cols="12"
                        sm="4"
                        md="4"
                        lg="2"
                        xs="2"
                        style="text-align:right"
                      >
                        <v-btn color="blue-grey" class="white--text" @click="getFrenteTrabalho()">Pesquisar</v-btn>
                      </v-col>
                      <v-col
                        cols="12"
                        sm="4"
                        md="4"
                        lg="3"
                        xs="3"
                        style="text-align:right"
                      >
                        <v-btn color="blue" class="white--text" @click="dialogFrenteTrabalho = true">+ Frente Trabalho</v-btn>
                      </v-col>
                    </template>
                  </v-row>
                  <v-row>
                    <template>
                      <v-col
                        cols="12"
                        md="12"
                        lg="12"
                        sm="12"
                        xs="12"
                      >
                        <template>
                          <v-data-table
                            :headers="headers"
                            :items="desserts"
                            :items-per-page="5"
                            class="elevation-1 color-table"
                            :loading="loadingTable"
                          >
                          <template v-slot:[`item.inativo`]="{ item }">
                            <v-btn
                              width="30px"
                              height="30px"
                              class="mr-5"
                              fab
                              dark
                              small
                              :color="item.inativo ? 'green' : 'red'"
                              @click="mudarStatusFrenteTrabalho(item)"
                            >
                            <v-icon dark small>
                              {{item.inativo ? 'mdi-play' : 'mdi-close'}}
                            </v-icon>{{ item.editar }}
                            </v-btn>
                          </template>
                          </v-data-table>
                        </template>
                      </v-col>
                      </template>
                  </v-row>
                </v-container>
              </v-card>
          </v-flex>
        </v-layout>

        <v-dialog
        v-model="dialogFrenteTrabalho"
          transition="dialog-top-transition"
          max-width="600"
        >
          <template>
            <v-card>
              <v-toolbar
                color="primary"
                dark
              >Adicionar Frente De Trabalho</v-toolbar>
              <v-card-text>
                <v-form
                  ref="form"
                >
                    <v-container fluid>
                    <v-row>
                      <v-col
                        cols="12"
                        sm="12"
                      >
                      <v-text-field
                        v-model="newName"
                        :rules="rules.name"
                        label="Nome da Frente De Trabalho"
                        required
                        clearable
                      ></v-text-field>
                      </v-col>
                    </v-row>
                    <v-row>
                      <v-col
                        cols="12"
                        sm="12"
                      >
                        <v-autocomplete
                          v-model="obra"
                          :rules="[rules.required]"
                          :items="arrayObra"
                          label="Obra"
                          hint="Selecionar a Obra"
                          clearable
                          solo
                        ></v-autocomplete>
                      </v-col>
                    </v-row>
                    </v-container>
                </v-form>
              </v-card-text>
              <v-card-actions class="justify-end">
                <v-btn
                  text
                  @click="closeModal()"
                >Close</v-btn>
                <v-btn
                  color="green"
                  text
                  @click="verificarFormulario()"
                >Salvar</v-btn>
              </v-card-actions>
            </v-card>
          </template>
        </v-dialog>

        <v-snackbar
          v-model="snackbar"
          :color="colorSnackbar"
        >
          {{ mensagem }}

          <template v-slot:action="{ attrs }">
            <v-btn
              color="white"
              text
              v-bind="attrs"
              @click="snackbar = false"
            >
              Close
            </v-btn>
          </template>
        </v-snackbar>
      </v-container>
      <div v-if="loader">
        <loader object="#ff9633" color1="#ffffff" color2="#17fd3d" size="5" speed="2" bg="#343a40" objectbg="#999793" opacity="80" name="circular"></loader>
      </div>
    </div>
</template>

<script>
import axios from 'axios'
export default {
  name: 'Funcao',
  data: () => ({
    urlProd: 'https://neexsa.com.br/api_rh/',
    // urlProd: 'http://localhost:4040/api_rh/',
    loader: false,
    newName: '',
    snackbar: false,
    dialogFrenteTrabalho: false,
    switchFrenteTrabalho: false,
    mensagem: '',
    colorSnackbar: '',
    textFilter: '',
    arrayObra: [],
    obra: '',
    desserts: [],
    camposVerificados: [],
    headers: [
      {
        text: 'Frente De Trabalho',
        align: 'start',
        sortable: false,
        value: 'frenteTrabalho'
      },
      {
        text: 'Obra',
        align: 'start',
        sortable: false,
        value: 'obra'
      },
      {
        text: 'Status',
        value: 'inativo',
        align: 'right',
        sortable: true,
        class: 'class-btn'
      }
    ],
    loadingTable: true,
    rules: {
      required: value => !!value || 'Obrigatrio.',
      name: [val => (val || '').length > 0 || 'Preencher o Nome da Obra']
    }
  }),
  created () {
    this.getFrenteTrabalho()
    this.getCient()
  },
  methods: {
    returnHome () {
      this.$router.push({ name: 'Home' })
    },
    async getCient () {
      const options = {
        url: `${this.urlProd}dominio/obras-ativos`,
        method: 'GET'
      }
      axios(options)
        .then(res => {
          this.arrayObra = res.data
        })
        .catch(err => {
          console.log(err)
          this.snackbar = true
          this.mensagem = err
          this.colorSnackbar = 'red'
          this.loadingTable = false
        })
        .finally(() => {
          this.loadingTable = false
        })
    },
    async getFrenteTrabalho () {
      this.loadingTable = true
      const options = {
        url: `${this.urlProd}get-frente-trabalho`,
        method: 'POST',
        data: {
          textFilter: this.textFilter ? this.textFilter : '',
          switchFrenteTrabalho: this.switchFrenteTrabalho
        }
      }
      axios(options)
        .then(res => {
          console.log(res)
          this.desserts = res.data
          console.log(this.desserts)
          this.loadingTable = false
        })
        .catch(err => {
          console.log(err)
          this.snackbar = true
          this.mensagem = err
          this.colorSnackbar = 'red'
          this.loadingTable = false
        })
        .finally(() => {
          this.loadingTable = false
        })
    },

    async verificarFormulario () {
      this.camposVerificados = []

      if (this.newName.length < 1) {
        this.camposVerificados.push('Nome da Frente De Trabalho')
      }

      if (this.obra.length < 1) {
        this.camposVerificados.push('Obra')
      }

      this.saveJob()
    },

    async saveJob () {
      if (this.camposVerificados.length === 0) {
        this.loader = true
        const options = {
          url: `${this.urlProd}nova-frente-trabalho`,
          method: 'POST',
          data: {
            newName: this.newName,
            obra: this.obra
          }
        }
        axios(options)
          .then(res => {
            if (res.data.qtd > 0) {
              this.snackbar = true
              this.mensagem = res.data.mensagem
              this.colorSnackbar = 'red'
              this.loader = false
            } else {
              this.snackbar = true
              this.mensagem = res.data.mensagem
              this.colorSnackbar = 'green'
              this.dialogFrenteTrabalho = false
              this.newName = ''
              this.obra = ''
              this.getFrenteTrabalho()
              this.loader = false
            }
          })
          .catch(err => {
            console.log(err)
            this.snackbar = true
            this.mensagem = err
            this.colorSnackbar = 'red'
            this.loader = false
          })
          .finally(() => {
            this.loader = false
          })
      } else {
        this.snackbar = true
        this.mensagem = 'Preencher o(s) campo(s): ' + this.camposVerificados.join(', ')
        this.colorSnackbar = 'red'
      }
    },

    async mudarStatusFrenteTrabalho (item) {
      this.loader = true
      const options = {
        url: `${this.urlProd}mudar-status-frente-trabalho`,
        method: 'POST',
        data: {
          frenteTrabalho: item.frenteTrabalho,
          obra: item.obra,
          switchFrenteTrabalho: !this.switchFrenteTrabalho
        }
      }
      axios(options)
        .then(res => {
          if (res.status === 500) {
            this.snackbar = true
            this.mensagem = res.data.mensagem
            this.colorSnackbar = 'red'
            this.dialogFrenteTrabalho = false
            this.loader = false
          } else {
            this.snackbar = true
            this.mensagem = res.data.mensagem
            this.colorSnackbar = 'green'
            this.dialogFrenteTrabalho = false
            this.getFrenteTrabalho()
            this.loader = false
          }
        })
        .catch(err => {
          console.log(err)
          this.snackbar = true
          this.mensagem = err
          this.colorSnackbar = 'red'
          this.loader = false
        })
        .finally(() => {
          this.loader = false
        })
    },

    closeModal () {
      this.dialogFrenteTrabalho = false
      this.newName = ''
      this.obra = ''
    }
  }
}
</script>

